<template>
    <v-card>
        <v-card-title class="text-button font-weight-black">
            <v-progress-circular indeterminate color="primary" v-show="loading">
            </v-progress-circular>
            收款单：{{ shoukuandan_id }}
            <v-spacer></v-spacer>
            <template v-if="skd_info.status == 1">
                <div class="red--text">已撤单：{{ skd_info.cancel_remark }}</div>
            </template>
            <template v-else-if="skd_info.status == 0">
                <v-form v-model="valid">
                    <v-text-field placeholder="撤单说明" v-model="cancel_remark" dense hide-details="auto" color="error"
                        :rules="[v => !!v]"></v-text-field>
                </v-form>
                <v-btn color="error" class="ml-5" small :disabled="!valid" @click.once="btn_cancel">
                    撤 单
                </v-btn>
            </template>
        </v-card-title>
        <v-card-text>
            <div style="border:1px solid #eee;background:#FFEBEE">
                <!-- 打印区域开始 -->
                <div class="print pa-4">
                    <!-- header -->
                    <div class="text-center text-h6 font-weight-black">
                        【{{ skd_info.xiaoqu.name }}】收费凭证</div>
                    <v-divider class="my-1"></v-divider>
                    <div class="d-flex justify-space-between text-button font-weight-black">
                        <span>房号：{{ skd_info.loudong.name + skd_info.danyuan.name + skd_info.fangwu.name }}</span>
                        <span :class="skd_info.status == 1 ? 'text-decoration-line-through' : ''">凭证：{{
                        skd_info._id
                        }}</span>
                        <span>业主：{{ skd_info.fangwu.yezhu }}</span>
                    </div>
                    <!-- content -->
                    <v-simple-table style="background:none;border:thin solid rgba(0, 0, 0, 0.38)" dense>
                        <template v-slot:default>
                            <thead>
                                <tr>
                                    <th class="text-center">项目</th>
                                    <th class="text-center">单价</th>
                                    <th class="text-center">起数</th>
                                    <th class="text-center">止数</th>
                                    <th class="text-center">周期/倍数</th>
                                    <th class="text-center">数量</th>
                                    <th class="text-center">总计</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="item in skd_info.feiyong" :key="item._id">
                                    <td class="text-center">{{ item.mingcheng }}</td>
                                    <td class="text-center">{{ item.danjia }}</td>
                                    <td class="text-center">{{ item.start }}</td>
                                    <td class="text-center">{{ item.end }}</td>
                                    <td class="text-center">{{ item.zhouqi_beishu }}</td>
                                    <td class="text-center">{{ item.shuliang }}</td>
                                    <td class="text-center">{{ item.zongji }}</td>
                                </tr>
                            </tbody>
                        </template>
                    </v-simple-table>
                    <!-- footer -->
                    <div>
                        <div class="d-flex justify-space-between text-button font-weight-black">
                            <span>应收：￥{{ yingshou }}{{ youhui != 0 ? ' - 优惠：￥' +
                            skd_info.youhui : ''
                            }} =
                                实收：￥{{ shishou }}</span>
                            <span>实收（大写）：{{ convertCurrency(shishou) }}</span>
                        </div>
                        <span>备注：{{ skd_info.remark }}</span>
                        <span class="float-right">收费员：{{ skd_info.user?skd_info.user.nickname:'无' }} @
                            收费方式：{{ skd_info.pay_way }} @ {{ skd_info.date_china }}</span>
                    </div>
                </div>
                <!-- 打印区域结束 -->
            </div>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="$emit('close')">
                关 闭
            </v-btn>
            <v-btn text>
                打 印
            </v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
export default {
    data: () => ({
        skd_info: {
            xiaoqu: {},
            loudong: {},
            danyuan: {},
            fangwu: {},
            user: {},
        },
        loading: false,
        yingshou: 0.00,
        youhui: 0.00,
        shishou: 0.00,
        cancel_remark: '',
        valid: false
    }),
    props: {
        shoukuandan_id: String
    },
    watch: {
        shoukuandan_id(val) {
            this.skd_info = {
                xiaoqu: {},
                loudong: {},
                danyuan: {},
                fangwu: {},
                user: {},
            }
            this.yingshou = 0.00
            this.youhui = 0.00
            this.shishou = 0.00
            this.cancel_remark = ''
            // 获取收款单详细信息
            if (val != '') {
                this.get_shoukuandan(val)
            }
        }
    },
    created() {
        // 首次挂载时先加载一次信息
        this.get_shoukuandan(this.shoukuandan_id)
    },
    methods: {
        get_shoukuandan(_id) {
            this.loading = true
            this.$axios.get('shoukuandan', { params: { _id: _id } })
                .then((res) => {
                    setTimeout(() => {
                        this.skd_info = res.data
                        this.yingshou = Number(res.data.total).toFixed(2)
                        this.youhui = Number(res.data.youhui).toFixed(2)
                        this.shishou = (this.yingshou - this.youhui).toFixed(2)
                        this.loading = false
                    }, 500);
                })
        },

        // 撤销
        btn_cancel() {

            this.$axios.post('/shoukuandan/cancel', { shoukuandan: this.skd_info, remark: this.cancel_remark })
                .then((res) => {
                    if (res.data.message == '撤销成功') {
                        this.get_shoukuandan(this.shoukuandan_id)
                    }
                })
        },

        // 转换大小写
        convertCurrency(n) {
            var fraction = ['角', '分'];
            var digit = [
                '零', '壹', '贰', '叁', '肆',
                '伍', '陆', '柒', '捌', '玖'
            ];
            var unit = [
                ['元', '万', '亿'],
                ['', '拾', '佰', '仟']
            ];
            var head = n < 0 ? '欠' : '';
            n = Math.abs(n);
            var s = '';
            for (let i = 0; i < fraction.length; i++) {
                s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/零./, '');
            }
            s = s || '整';
            n = Math.floor(n);
            for (let i = 0; i < unit[0].length && n > 0; i++) {
                var p = '';
                for (let j = 0; j < unit[1].length && n > 0; j++) {
                    p = digit[n % 10] + unit[1][j] + p;
                    n = Math.floor(n / 10);
                }
                s = p.replace(/(零.)*零$/, '').replace(/^$/, '零') + unit[0][i] + s;
            }
            return head + s.replace(/(零.)*零元/, '元')
                .replace(/(零.)+/g, '零')
                .replace(/^整$/, '零元整');
        }
    }
}
</script>