<template>
  <v-main>
    <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}</v-snackbar>
    <v-container>
      <v-row style="height: 90vh" align="center" justify="center">
        <v-col cols="12" sm="6" md="4" lg="3">
          <v-card elevation="24" color="rgba(255, 255, 255, 0.9)">
            <v-card-title class="flex-column justify-center">
              <div><img src="../../public/logo.png" style="width:50px;"></div>
              <div>大杨通物业云管家</div>
            </v-card-title>
            <v-card-subtitle class="text-center"> -- 专业易用的物业云管理平台 -- </v-card-subtitle>
            <v-card-text>
              <v-text-field autofocus clearable v-model="form.username" outlined prepend-inner-icon="mdi-account" label="用 户 名">
              </v-text-field>
              <v-text-field @click:append="show = !show" :type="show ? 'text' : 'password'"
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" v-model="form.password" outlined prepend-inner-icon="mdi-key" label="密 码">
              </v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn :loading="loading" class="float-right" color="primary" @click="login">
                登 录
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <div class="footer d-flex justify-center align-center">
        <div style="margin-right: 20px;"><h1>DAYANGTONG.CN</h1></div>
        <div style="margin-right: 20px;color: #bbb;">
          <div>版权所有：大杨通网络科技有限公司</div>
          <div>销售咨询：183 - 0681 - 7521 售后服务：183 - 0681 - 7521</div>
        </div>
        <img style="margin-right: 20px;" src="../../public/foot-gfwx01.png">
        <img style="margin-right: 20px;" src="../../public/foot-gfwx01.png">
    </div>
    <v-overlay :value="loading">
      <v-progress-circular indeterminate size="64" color="primary"></v-progress-circular>
    </v-overlay>
  </v-main>
</template>

<script>
export default {
  name: "LoginView",
  data: () => {
    return {
      show: false,
      snackbar: {
        show: false,
        text: "",
        color: "success"
      },

      loading: false,
      form: {}
    }
  },
  methods: {
    login() {
      if (!this.form.username || !this.form.password) {
        this.snackbar = {
          show: true,
          text: "用户名和密码不能为空！",
          color: "error"
        }
      } else {
        this.loading = true
        this.$axios.post('/user/login', this.form)
          .then(res => {
            console.log(res.data)
            if (res.data.token) {
              sessionStorage.setItem('token', res.data.token)
              sessionStorage.setItem('userid', res.data.userid)
              sessionStorage.setItem('nickname', res.data.nickname)
              sessionStorage.setItem('company', JSON.stringify(res.data.company))
              setTimeout(() => {
                this.loading = false
                this.$router.push('/home')
              }, 2000);
            } else {
              setTimeout(() => {
                this.loading = false
                this.snackbar = {
                  show: true,
                  text: res.data.message,
                  color: res.data.errors ? "error" : "success",
                }
              }, 1000);
            }
          })
          .catch(error => {
            this.snackbar = {
              show: true,
              text: error,
              color: "error"
            }
            this.loading = false
          })
      }
    }
  }
};
</script>
<style scoped>
.v-main {
  background: url(../../public/login_bg.jpg) center top no-repeat;
}

.footer {
  background: #333;
  color: #fff;
  position: absolute;
  width: 100%;
  bottom: 0;
  padding:10px 0;
}
</style>